import dnIcon from '@/components/dn-icon/dn-icon.vue'

export default {
	components: {
		dnIcon
	},
	props: {

		/* popup弹出位置*/
		position: {
			type: String,
			default: 'bottom'
		},

		/* popup高度 按照750*1334设计稿的尺寸 会自动计算适配 */
		height: {
			type: String,
			default: '850'
		},

		/* popup header 取消文字 */
		cancelText: {
			type: String,
			default: ""
		},

		/* popup header 确认文字 */
		confirmText: {
			type: String,
			default: ""
		},

		/* popup header title标题 */
		titleText: {
			type: String,
			default: ""
		}
	},
	computed: {
		popupHeight() {
			return uni.upx2px(this.height) + 'px';
		}
	},
	data() {
		let offsetTop = 0;



		return {
			offsetTop: offsetTop,
			showPopup: false,
			list: [],
			currItem: {} // 当前选中的
		}
	},
	methods: {
		// 点击蒙层
		hide: function() {
			this.showPopup = false;
			this.$emit('hidePopup');
		},

		// 点击取消按钮
		clickCancel() {
			this.showPopup = false;
			this.$emit("cancel")
		},

		// 点击确认按钮
		clickConfirm() {
			this.showPopup = false;
			this.$emit("confirm")
		},

		// 通过refs调用打开
		show() {
			this.showPopup = true;
			// 容错处理
			this.list.length || this.getCardList();
		},

		// 通过refs调用关闭
		close() {
			this.showPopup = false;
		},

		// 滚动穿透阻止
		moveHandle(e) {
			return false;
		},

		// 滚动事件
		scroll(e) {
			this.$emit("scroll", e)
		},

		// 点击item触发
		async clickItem(item) {
			this.showPopup = false;
			this.setCurrSelect(item);
		},

		// 设置当前选中
		setCurrSelect(item) {
			this.currItem = item;
			this.$emit("onSelect", item);
		},

		// 跳转信用卡添加
		toUrl() {
			this.showPopup = false;

			this.$mRouter.push({
				route: this.$mRoutesConfig.ccAdd
			})
		},

		// 加载列表数据
		async getCardList() {
			try {
				// let data = await this.$apis.getCardList();
				// this.list = data.data.reverse();
				this.list = [{
					"cardType": "CC",
					"bankName": "中国工商银行",
					"cardNo": 8945703346386633000,
					"reservedPhoneNo": "13522199952",
					"accountName": "张三",
					"onlyCode": "11111"
				}, {
					"cardType": "CC",
					"bankName": "中国工商银行",
					"cardNo": 8192899225053531000,
					"reservedPhoneNo": "13522199952",
					"accountName": "张三",
					"onlyCode": "11111"
				}]
				this.setDefaultSelect();
			} catch (e) {
				//TODO handle the exception
				console.log('zxc')
				this.showPopup = false;
			}
		},


		// 设置默认选中
		setDefaultSelect() {
			if (this.list instanceof Array && this.list.length !== 0) this.setCurrSelect(this.list[0]);
		},
		
		//路由跳转
		handleGoRouter (url) {
			uni.navigateTo({
				url: url,
				success: res => {},
				fail: () => {},
				complete: () => {}
			})
		}
	}
}
